.selector-button {
    margin: 6px;
    padding: 8px 14px;
    background-color: #007acc;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-size: 13px;
}

.selector-button:hover {
    background-color: #005b99;
}

#selection-table {
    margin-top: 12px;
    border-collapse: collapse;
    width: 100%;
}

#selection-table th,
#selection-table td {
    border: 1px solid #ccc;
    padding: 6px;
    text-align: left;
}
